import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class SubmissionService {

    constructor(public http: HttpClient, public storage: Storage) { }
    
  
    public getSubmissions(search){
        return new Promise((resolve, reject) => {
            this.storage.get("ht_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/submissions?token=" + token + "&search=" + search).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }      
    

    
    public getSubmission(submissionId){
        return new Promise((resolve, reject) => {
            this.storage.get("ht_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/submissions/" + submissionId + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    public createSubmission(submission, formId){
        return new Promise((resolve, reject) => {
            this.storage.get("ht_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/submissions?token=" + token, {fields:submission, form_id:formId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }               
}
