import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OfflineService {

    constructor(public http: HttpClient, public storage: Storage) { }
    
  
    public saveForms(forms){
        this.storage.set("ht_forms", forms);       
    }      
    
    public getForms(){
        return this.storage.get("ht_forms");
    }
    

    public saveFormGroups(formGroups){
        this.storage.set("ht_form_groups", formGroups);       
    }      
    
    public getFormGroups(){
        return this.storage.get("ht_form_groups");
    }
        
    
    public saveDocuments(documents){
        this.storage.set("ht_documents", documents);       
    }
    
    public getDocuments(){
        return this.storage.get("ht_documents");
    }
    
    
    public saveDocumentGroups(documentGroups){
        this.storage.set("ht_document_groups", documentGroups);       
    }      
    
    public getDocumentGroups(){
        return this.storage.get("ht_document_groups");
    }    
    
    
    
    public saveSubmission(submission){
        this.storage.get("ht_submissions").then((submissions) => {
            if (!submissions){
                submissions = [];
            }
            
            submissions.push(submission);
            
            
            this.storage.set("ht_submissions", submissions);
            
            
        });  
    }
    
    public saveSubmissions(submissions){

        this.storage.set("ht_submissions", submissions);

    }    
    
    
    public getSubmissions(){
        return this.storage.get("ht_submissions");
    }
    
    
}